body {
  --main-color: #00acdc;
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.img__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  align-content: center;
  height: 60px;
  margin-left: 20px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-content: center;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
